body,#root>div{
  margin :0;
  border: 0;
  padding:0;
  position:relative;
  display: flex;
  flex-direction:column;
  justify-content: center;
  align-items: center;
  font-family: 'Kurye';
}

@font-face {
  font-family: 'Kurye';
  font-style: normal;
  font-weight: normal;
  src: local('Kurye'), url('Kurye-Light.otf') format('opentype');
}

#root{
  width: 100%;
}





#mask{
  width:263px;
  transition: 0.2s
}

@keyframes soundBar {
  0%{width:0px;}
  100%{width:263px;}
}

#mask.start{
  width:0;
  animation: soundBar 5s;
  animation-fill-mode: forwards;
}

#bottom_separator{
  height: 20vh;
}

.ContentPentagrama{
  top:170px;
}