  /* Animacino Pasiva */
  .Wave1{animation: shake 5s infinite;}
  .Wave2{animation: antishake 5s infinite;}

  @keyframes shake {
    0%,100%{transform: translateX(0)translateY(-1px);}
    50%{transform: translateX(10px)scaleY(1.4)translateY(1px);}
  }

  @keyframes antishake {
    0%,100%{transform: translateX(10px)scaleY(1.4)translateY(1px);}
    50%{transform: translateX(0);}
  }

svg{
  transition: all 500;
}

.MainWave, .Background{
