.Overlay{
    transition: 0.5s;
    width: 100vw;
    height: 100vh;
    position: fixed !important;
    left:0px;
    top:0px;
    display: flex;
    justify-items: center;
    justify-content: center;
    align-items: center;
    align-content: center;
    flex-direction: column;

}

.OverlayListItem{
    display: flex;
    flex-direction: row;
    width: 60vw;
    align-items: center;
    justify-content: space-around;
    z-index: 10;
    background:rgb(255, 30, 97);
    border: rgb(255, 30, 97);
    border-radius: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.OverlayListItem > .Text , .OverlayListItem > .Icon {
    font-family: 'Kurye';
    color:#CFCBD1;

}
.Shadow{
    background: rgb(4, 4, 4);
    opacity: 50%;
    width: 110%;
    height: 110%;
    position: absolute;
    border-radius: 30%;
    backdrop-filter: blur(30px);
    -webkit-backdrop-filter: blur(30px);
}

.Rect{
    position:absolute;
    width: 60vw;
    height: 30vh;
    /* color: rgb(255, 30, 97); */
    /* background-color: rgb(255, 30, 97); */
    opacity: 100%;
    border-radius: 20px;
}


.liquid-enter {
    opacity: 0.01;
    transform: translate(-40px, 0)
}

.liquid-enter-active {
    opacity: 1;
    transform: translate(0, 0);
    transition: all 500ms ease-in;
}

.liquid-exit {
    opacity: 1;
    transform: translate(0, 0)
}

.liquid-exit-active {
    opacity: 0.01;
    transform: translate(40px, 0);
    transition: all 500ms ease-in;
}
